@import './assets/variables.css';
@import './assets/globals.css';

#root,
.ss-wrapper {
	height: 100%;
}

@-webkit-keyframes rotate {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotate {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@-webkit-keyframes rotate-reversed {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(-360deg);
		        transform: rotate(-360deg);
	}
}

@keyframes rotate-reversed {
	0% {
		-webkit-transform: rotate(0);
		        transform: rotate(0);
	}

	100% {
		-webkit-transform: rotate(-360deg);
		        transform: rotate(-360deg);
	}
}

@-webkit-keyframes show-content {
	0% {
		opacity: 0;
		-webkit-transform: translateY(30px);
		        transform: translateY(30px);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

@keyframes show-content {
	0% {
		opacity: 0;
		-webkit-transform: translateY(30px);
		        transform: translateY(30px);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}
}

.ss-wrapper {
	display: -ms-flexbox;
	display: flex;

	&.loaded {
		& .ss-container {
			& .ss-content {
				-webkit-animation: show-content 1s 1 forwards ease .3s;
				        animation: show-content 1s 1 forwards ease .3s;
			}
		}
	}

	& .ss-container {
		margin: auto;
		width: 100%;
		padding-top: 200px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;

		& .ss-header {
			text-align: center;
			width: 100%;
			margin: 0;
			position: absolute;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			    -ms-transform: translate(-50%, -50%);
			        transform: translate(-50%, -50%);
			-webkit-transition: all .5s ease;
			-o-transition: all .5s ease;
			transition: all .5s ease;

			&.loaded {
				top: 0;
				-webkit-transform: translate(-50%, 0);
				    -ms-transform: translate(-50%, 0);
				        transform: translate(-50%, 0);

				& .ss-logo {
					width: 275px;

					& .ss-first,
					& .ss-second,
					& .ss-name {
						-webkit-animation: none;
						        animation: none;
						left: 0;
					}

					& .ss-first {
						top: 25px;
						-webkit-transform: rotate(90deg) scale(.6);
						    -ms-transform: rotate(90deg) scale(.6);
						        transform: rotate(90deg) scale(.6);
					}

					& .ss-second {
						-webkit-transform: rotate(90deg) scale(.6);
						    -ms-transform: rotate(90deg) scale(.6);
						        transform: rotate(90deg) scale(.6);
						top: 52px;
					}

					& .ss-name {
						opacity: 1;
						top: 87px;
						left: 96px;
						-webkit-transition: all 1s ease;
						-o-transition: all 1s ease;
						transition: all 1s ease;
						-webkit-transition-delay: .3s;
						     -o-transition-delay: .3s;
						        transition-delay: .3s;
					}
				}
			}

			& .ss-logo {
				width: 204px;
				height: 200px;
				position: relative;
				margin: auto;
				-webkit-transition: all .5s ease;
				-o-transition: all .5s ease;
				transition: all .5s ease;

				& .ss-first,
				& .ss-second,
				& .ss-name {
					position: absolute;
					-webkit-animation-fill-mode: forwards;
					        animation-fill-mode: forwards;
					left: 0;
					-webkit-transition: all .5s ease;
					-o-transition: all .5s ease;
					transition: all .5s ease;
				}

				& .ss-name {
					opacity: 0;
					left: 116px;
				}

				& .ss-first,
				& .ss-second {
					left: 20px;
					-webkit-transform-origin: center center;
					    -ms-transform-origin: center center;
					        transform-origin: center center;
				}

				& .ss-first {
					top: 40px;
					left: 20px;
					-webkit-animation: rotate 1s infinite forwards ease;
					        animation: rotate 1s infinite forwards ease;
				}

				& .ss-second {
					-webkit-animation: rotate-reversed 1s infinite forwards ease;
					        animation: rotate-reversed 1s infinite forwards ease;
					top: 30px;
					left: 70px;
				}
			}
		}

		& .ss-content {
			max-width: 640px;
			width: calc(100% - 40px);
			margin: 0 auto 20px;
			background: var(--light-blue);
			display: -ms-flexbox;
			display: flex;
			-ms-flex-wrap: wrap;
			    flex-wrap: wrap;
			-webkit-box-sizing: border-box;
			        box-sizing: border-box;
			padding: 0;
			border-radius: 10px;
			border: solid 1px var(--medium-blue);
			-webkit-transition: all .5s ease;
			-o-transition: all .5s ease;
			transition: all .5s ease;
			opacity: 0;
			-webkit-transform: translateY(30px);
			    -ms-transform: translateY(30px);
			        transform: translateY(30px);
			-webkit-transition-delay: 1s;
			     -o-transition-delay: 1s;
			        transition-delay: 1s;

			@media (--screen-mobile) {
				margin: 20px;
				width: calc(100% - 40px);
			}

			& section {
				-ms-flex-preferred-size: 50%;
				    flex-basis: 50%;
				-webkit-box-sizing: border-box;
				        box-sizing: border-box;
				padding: 0px;

				&.ss-time {
					width: 100%;
					-ms-flex-preferred-size: 100%;
					    flex-basis: 100%;
					text-align: center;
					color: var(--dark-blue);
					font-size: var(--small-font-size);

					& div {
						border-bottom: none;
					}
				}
				
				&:first-child {
					border-right: solid 1px var(--medium-blue);
				}

				& div {
					-webkit-box-sizing: border-box;
					        box-sizing: border-box;
					padding: 10px 20px;
					width: 100%;
					border-bottom: solid 1px var(--medium-blue);
					vertical-align: middle;

					& i {
						vertical-align: middle;
						font-style: normal;
						font-size: var(--smaller-font-size);
						float: right;
						display: inline-block;
						color: var(--dark-blue);
						-webkit-box-sizing: border-box;
						        box-sizing: border-box;
						padding: 2px 0;

						&:after {
							content: '';
							display: inline-block;
							width: 12px;
							height: 12px;
							border-radius: 50%;
							background: var(--black);
							vertical-align: middle;
							margin-left: 5px;
						}

						&.online {
							&:after {
								background: var(--success-color);
							}
						}

						&.offline {
							&:after {
								background: var(--error-color);
							}
						}
					}
				}
			}
		}
	}
}


